@charset "UTF-8";
* {box-sizing:border-box;}
body, h1, h2, h3, h4, h5, h6, div, p, span, button,
ul, ol, li, dl, dt, dd, form, fieldset, input, select, label, textarea, img, a {
	margin:0; padding:0; border:0; word-break:break-all; word-wrap:break-word;
	font-size:14px;
	font-weight:normal;
	color:#222;
}
h1, h2, h3, h4, h5, h6 {font-weight:normal;}
img, input, select, textarea, button {vertical-align:top;}
legend, caption {
	overflow:hidden; visibility:hidden; height:0; z-index:-1; text-indent:-9999px; font-size:0; line-height:0;
}
button {background:none; cursor:pointer;}
strong {color:#212121;}
a {text-decoration:none;}
a:focus, a:hover, a:active {text-decoration:none;}
ul, ol {list-style:none;}
label {cursor:pointer;}
i {font-style:normal}
iframe {border:0;}
select {padding:1px 8px 2px; border:1px solid #e1e1e1;border-top:1px solid #aaadb2; background: #fff; cursor:pointer;vertical-align: middle}
textarea {padding:5px 10px; border:1px solid #e1e1e1; resize:none;border-top-color:#999}
input[type="text"],
input[type="password"],
select {
	padding:0 6px; border:1px solid #d6d6d6; border-radius:2px;
	font-size:13px; color:#717171;
	border-top:1px solid #999999;
	vertical-align: middle;
	line-height:26px;
	height:26px;
}

input[type="checkbox"], input[type="radio"] {vertical-align:middle;position:relative;top:-1px}
input[type="text"], textarea {ime-mode:active;}
input[readonly='readonly'] {
	background:#fafafa;
}
input+input,
input+button,
select+input,
select+select,
label+button,
label+label,
label+input,
span+input {margin-left:5px;}

h1 {font-size:32px;}
h2 {font-size:22px;}

.ie {
	select {padding:1px 1px 2px 8px;}
}

label {
	input[type="checkbox"], input[type="radio"] {
		margin-right: 5px;
	}
}