.txtHidden {
  @include txtHidden();
}
input.txtHidden {
  @include txtHidden();
  width:0 !important; height:0 !important;
  line-height:0 !important;
  border:none !important;
}
h1.txtHidden {
  height:0 !important;
  font-size:0 !important;
  line-height:0 !important;
  margin: 0 !important;
  padding:0 !important;
  display:block !important;
}